import React, { useState, useEffect } from 'react';
import { ethers } from 'ethers';
import { prepare, request, getResult } from 'klip-sdk';
import QRCode from 'qrcode.react';
import Modal from 'react-modal';

import present from '../assets/present.png';
import soldOut from '../assets/end.png';

import addresses from '../config/addresses.json';
import storage from '../config/localstorage.json';

import './Airdrop.css';

function Membership() {
  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [qrValue, setQrValue] = useState('');
  const [isMembership, setIsMembership] = useState(false);
  const [isPurchase, setIsPurchase] = useState(false);
  const [totalSupply, setTotalSupply] = useState(0);
  const [memberMetadata, setMemberMetadata] = useState([]);

  const rwaAddress = addresses.rwaNft;
  const nftAddress = addresses.membershipNft;
  const mintAbi = '{"inputs":[],"name":"mint","outputs":[],"stateMutability": "nonpayable","type":"function"}';
  const nftAbi = [{
    "inputs": [],
    "name": "tokenCounter",
    "outputs": [
      {
        "internalType": "uint256",
        "name": "",
        "type": "uint256"
      }
    ],
    "stateMutability": "view",
    "type": "function"
  }, {
    "inputs": [
      {
        "internalType": "address",
        "name": "owner",
        "type": "address"
      }
    ],
    "name": "balanceOf",
    "outputs": [
      {
        "internalType": "uint256",
        "name": "",
        "type": "uint256"
      }
    ],
    "stateMutability": "view",
    "type": "function"
  },
  {
    "inputs": [],
    "name": "getUsersNickname",
    "outputs": [
      {
        "internalType": "string[]",
        "name": "",
        "type": "string[]"
      }
    ],
    "stateMutability": "view",
    "type": "function"
  },];

  useEffect(() => {
    const provider = new ethers.JsonRpcProvider("https://public-en.node.kaia.io");
    const rwaContract = new ethers.Contract(rwaAddress, nftAbi, provider);
    const nftContract = new ethers.Contract(nftAddress, nftAbi, provider);

    const checkMembershipStatus = async () => {
      try {
        const userAdderss = localStorage.getItem(storage.klip);
        if (userAdderss == '') {
          setIsPurchase(false);
        } else {
          const purchaseStatus = await nftContract.balanceOf(userAdderss);
          setIsMembership(purchaseStatus);

          const membershipStatus = await rwaContract.balanceOf(userAdderss);
          if (membershipStatus) {
            console.log("membershipStatus", membershipStatus)
            setIsPurchase(true);
            const totalCount = await rwaContract.tokenCounter();
            setTotalSupply(totalCount);
            const metadata = await rwaContract.getUsersNickname();
            setMemberMetadata(metadata);
          } else {
            setIsPurchase(false);
          }
        }
      } catch (error) {
        // console.error('Error checking membership status:', error);
      }
    };

    // 10 
    checkMembershipStatus();
    const intervalId = setInterval(checkMembershipStatus, 10000);

    return () => clearInterval(intervalId);
  }, []);

  const openModal = () => {
    setModalIsOpen(true);
  };

  const closeModal = () => {
    setModalIsOpen(false);
  };

  const mintNFT = async () => {
    const bappName = 'MODULAB DAPP';

    // Step 1: Prepare the contract action
    const transaction = {
      bappName,
      to: rwaAddress,
      abi: mintAbi,
      value: '0',
      params: '[]',
      from: localStorage.getItem(storage.klip),
    };

    // Step 2: Request the contract action through Klip
    const { request_key } = await prepare.executeContract(transaction);
    // const res = await axios.post(A2P_API_PREPARE_URL, {
    //   bapp: { name: bappName, },
    //   transaction,
    //   type: "execute_contract",
    // })
    // const { request_key } = res.data;

    if (!isPurchase) {
      const userAgent = navigator.userAgent;
      if (/Windows/i.test(userAgent) || /Macintosh/i.test(userAgent)) {
        const qrURL = `https://klipwallet.com/?target=/a2a?request_key=${request_key}`;
        // window.open(qrURL, '_blank');
        setQrValue(qrURL);

        openModal();
      } else {
        request(request_key);
      }
    }

    // Step 3: Poll for the result
    // const interval = setInterval(() => {
    //   getResult(request_key, (result) => {
    //     if (result.err) {
    //       console.error(result.err);
    //       clearInterval(interval);
    //       return;
    //     }
    //     if (result.result) {
    //       clearInterval(interval);
    //     }
    //   });
    // }, 1000);
  };

  return (
    <div className='airdrop-body'>
      {totalSupply < 3 ? (
        // {totalSupply < 1 ? (
        <div className='airdrop-body-wrapper'>
          <div className='airdrop-body-text'> !</div>
          <img className='modu-nft-image' style={{ width: 259, height: 259 }} src={present} />
          <div className='airdrop-body-text-sub'>    !   <span style={{ fontWeight: 'bold' }}> 3</span>   .    RWA NFT    . ,   !</div>
          {isMembership ? (
            <div className='airdrop-body-text-sub2-wrapper'>
              <div className='airdrop-body-text-sub2'>* : {3 - totalSupply.toString()}</div>
              <button className={isPurchase ? 'airdrop-button-inactive' : 'airdrop-button'} onClick={mintNFT}>
                <div className='faucet-button-text'>{isPurchase ? ' ' : ''}</div>
              </button>
            </div>
          ) : (
            <div className='airdrop-body-text-sub2-wrapper'>
              <div className='airdrop-body-text-sub3'>*  </div>
            </div>)}
          <Modal
            isOpen={modalIsOpen}
            onRequestClose={closeModal}
            className="modal-content"
            overlayClassName="modal-overlay"
            contentLabel="QR Code Modal"
          >
            <div className='airdrop-body-text'>QR  </div>
            <QRCode value={qrValue} size={256} />
          </Modal>
        </div>
      ) : (
        <div className='airdrop-body-wrapper'>
          <div className='airdrop-body-text'> </div>
          <img className='modu-nft-image' style={{ width: 259, height: 259 }} src={soldOut} />
          <div className='airdrop-body-text-sub'>! <span style={{ fontWeight: 'bold' }}>{memberMetadata[0]}, {memberMetadata[1]}, {memberMetadata[2]}</span>   .      .     !</div>
          {/* <div className='airdrop-body-text-sub'>! <span style={{ fontWeight: 'bold' }}>{memberMetadata[0]}, {memberMetadata[0]}, {memberMetadata[0]}</span>   .      .     !</div> */}
        </div>
      )}
    </div>
  );
}

export default Membership;
